import { AbiItem } from 'web3-utils'
import { Interface } from '@ethersproject/abi'
import { getWeb3 } from 'utils/web3'
import MultiCallAbi from 'config/abi/Multicall.json'
import { getMulticallAddress } from 'utils/addressHelpers'

interface Call {
  address: string // Address of the contract
  name: string // Function name on the contract (exemple: balanceOf)
  params?: any[] // Function params
}

const multicall = async (abi: any[], calls: Call[]) => {
  const web3 = getWeb3()
  const multi = new web3.eth.Contract((MultiCallAbi as unknown) as AbiItem, getMulticallAddress())
  const itf = new Interface(abi)
  console.log("We are in multicall!!");
  calls.map((call) => [console.log("multicallAddress:", getMulticallAddress()),console.log("calladdress:", call.address), console.log("callNAME:", call.name), console.log("callParams", call.params)] );
  
  const calldata = calls.map((call) => [call.address.toLowerCase(), itf.encodeFunctionData(call.name, call.params)])
  const { returnData } = await multi.methods.aggregate(calldata).call()// error
  console.log("we are here line 22!!", returnData);

  const res = returnData.map((call, i) => itf.decodeFunctionResult(calls[i].name, call)) // error
  console.log("We are in multicall222222!!");
  return res
}

export default multicall
